	module.exports = {"data":{"site":{"siteMetadata":{"title":"I don't proof read.","author":"Matt Carrick"}},"markdownRemark":{"id":"/Users/mattcarrick/blog/src/pages/2017-05-15-beautiful-new-tab-initial-release/index.md absPath of file >>> MarkdownRemark","html":"<h2>Version 1.1</h2>\n<p>  \n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/1-4f6132c63348274ca1f4c713e01243bf-275c2.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 62.5%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Pretty\"\n        title=\"\"\n        src=\"/static/1-4f6132c63348274ca1f4c713e01243bf-fb8a0.png\"\n        srcset=\"/static/1-4f6132c63348274ca1f4c713e01243bf-1a291.png 148w,\n/static/1-4f6132c63348274ca1f4c713e01243bf-2bc4a.png 295w,\n/static/1-4f6132c63348274ca1f4c713e01243bf-fb8a0.png 590w,\n/static/1-4f6132c63348274ca1f4c713e01243bf-526de.png 885w,\n/static/1-4f6132c63348274ca1f4c713e01243bf-fa2eb.png 1180w,\n/static/1-4f6132c63348274ca1f4c713e01243bf-275c2.png 1280w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p> Yesterday I released v1.0 of <a href=\"https://chrome.google.com/webstore/detail/beautiful-new-tab/bailppllbndlpphoipdchcmiiefhelfh\">my Chrome extension</a>, and after finding a bug immediately after publishing it on the web store, I uploaded a fixed version 1.1 shortly afterwards. Version 1.2 is currently being published with another minor fix too.</p>\n<p>This means it’s done™! Not really though. It’s not perfect and it does need some work, but it’s polished enough to release now at least.</p>\n<p>If you don’t know - it changes the Chrome new tab page to another, much prettier page. It shows a random image from Unsplash, which are almost always gorgeous, as well as the date and time and the temperature and weather, which is pulled from OpenWeatherMap.</p>\n<p>It’s easy to install if you’re on Chrome - just click “Add To Chrome” from the <a href=\"https://chrome.google.com/webstore/detail/beautiful-new-tab/bailppllbndlpphoipdchcmiiefhelfh\">extension’s page</a> to download it. That’s it, but you’ll probably want to change its options, like which links it displays, from the options page.</p>\n<h2>What isn’t good</h2>\n<p>As I said, it’s not perfect, particularly, the icon really needs changing. I’m just using a material cloud icon at the moment which doesn’t really make any sense or fit or anything but look I’m no graphic designer.</p>\n<p>It could use a better name too, I wasn’t very creative with <em>Beautiful New Tab</em>. I was jokingly thinking of <em>Inertia</em> because it’s a play on <em>Momentum</em> (the popular competitor extension).</p>\n<p>If you have a good name suggestions seriously let me know (matt at rokco dot org), or if you want to donate an icon I would obviously be very grateful :smile:.</p>\n<h2>Anything else?</h2>\n<p>You can look at the source on <a href=\"https://github.com/xRokco/Beautiful-New-Tab\">my GitHub</a> if you like.</p>\n<p>I might make a more personal blog post, or a post about what else I’ve been doing or working on later or tomorrow.</p>","frontmatter":{"title":"Initial Release of my Beautiful New Tab","date":"May 15, 2017"}}},"pathContext":{"path":"/beautiful-new-tab-initial-release/"}}

